.effect-border {
    display: block;
    position: relative;
    overflow: hidden
}

.effect-border:after,
.effect-border:before {
    content: "";
    position: absolute;
    top: 5px;
    bottom: 5px;
    left: 5px;
    right: 5px;
    border-style: solid;
    border-color: #fff;
    -webkit-transition: .3s;
    transition: .3s;
    z-index: 100
}

.effect-border:before {
    border-width: 1px 0;
    -webkit-transform: scale(0, 1);
    transform: scale(0, 1);
    top: 10px;
    bottom: 10px;
    left: 5px;
    right: 5px
}

.effect-border:after {
    border-width: 0 1px;
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    left: 10px;
    right: 10px
}

.effect-border img {
    display: block;
    width: 100%;
    -webkit-transition: .35s;
    transition: .35s
}

.effect-border:hover:after,
.effect-border:hover:before {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    -webkit-transition: .3s;
    transition: .3s
}

.effect-border:hover img {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    -webkit-transition: .35s;
    transition: .35s
}

.effect-bg {
    position: relative;
    display: block
}

.effect-bg:before {
    content: "";
    background-color: #f36;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    opacity: 0;
    border-radius: 0;
    -webkit-transform: scale(.3);
    transform: scale(.3);
    -webkit-transition: .35s;
    transition: .35s
}

.effect-bg img {
    display: block;
    width: 100%
}

.effect-bg:hover:before {
    opacity: .4;
    border-radius: 0;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: .35s;
    transition: .35s
}

.effect-banner1 .box-img {
    position: relative;
    display: block
}

.effect-banner1 .box-img:after,
.effect-banner1 .box-img:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: .6s;
    transition: .6s
}

.effect-banner1 .box-img:hover:before {
    top: 50%;
    bottom: 50%;
    -webkit-transition: .6s;
    transition: .6s;
    background-color: rgba(255, 255, 255, .5)
}

.effect-banner1 .box-img:hover:after {
    left: 50%;
    right: 50%;
